import React from 'react';
import useFormless from "react-useformless";

import { ProfileForm } from "./ProfileForm";
import { AddressForm } from "./AddressForm";
import { StateDataFormless } from "./StateDataFormless";

export default (props) => {
  const { inputProps, inputCheckboxProps, values, touched, errors, isValid, party, reset, onSubmit, validateParty } = useFormless({
    initialValues: {

    },
    onSuccess: (ev) => {
      ev.preventDefault()
      console.log('success')
    },
    onError: (ev) => {
      ev.preventDefault()
      console.log('error')
    },
    validate: (name, value) => {
      const validations = {
        terms: value === false && 'you must accept terms!',
        aboutyou: value === '' && 'please tell us a little bit about you',
      }
      return validations[name] || ''
    }
  })

  return (
    <>
      <form onSubmit={onSubmit} {...props}>
        <ProfileForm party={party}/>
        <AddressForm party={party} />
        <div class="field">
          <div class="control">
            <textarea className="textarea" {...inputProps('aboutyou')} placeholder="tell us something interesting about you ^^" cols="80" rows="10"/>
          </div>
        </div>
        <div class="field">
          <div class="control">
            <label htmlFor="terms" className="checkbox">
              <input id="terms" type="checkbox" {...inputCheckboxProps('terms', false)} /> you accept terms and conditions
            </label>
          </div>
        </div>
        <div className="field">
          <div class="buttons has-addons">
            <button className="button is-primary" type="button" onClick={reset}>reset</button>
            <button className="button is-info" type="submit">Send</button>
          </div>
        </div>
      </form>
      <StateDataFormless className="column" values={values} touched={touched} errors={errors}/>
    </>)
}